header {
  background-color: rgba(39, 39, 37, 0.8);
  position: fixed;
  width: 100%;
  .set-height {
    height: 120px;
  }
  .nav {
    font-size: 13px;
    font-weight: bold;
    color: #b8b8b8;
    text-transform: uppercase;
    ul {
      margin: 0;
      padding: 0;
      list-style: none;
    }
    li {
      margin: 0 15px 0 0;
      padding: 0;
      &:last-child {
        margin-right: 0;
      }
      &:hover {
        cursor: pointer;
      }
      &.active {
        color: $active-color;
        position: relative;
        &:after {
          content: "";
          position: absolute;
          z-index: 2;
          left: 0;
          right: 0;
          bottom: -52px;
          height: 2px;
          background-color: $active-color;
          display: block;

        }
      }
    }

  }

}




